.accordion-menu .icon-caretDown, .accordion-menu .icon-caretLeft, .accordion-menu .icon-caretRight {
  width: 1rem;
  height: 1rem;
  margin: 0 0 .1rem 0;
}

.accordion-menu .icon-caretRight:before, .accordion-menu .icon-caretRight:after,
.accordion-menu .icon-caretLeft:before, .accordion-menu .icon-caretLeft:after,
.accordion-menu .icon-caretDown:before, .accordion-menu .icon-caretDown:after {
  width: 5px;
  height: 1px;
  position: absolute;
  bottom: 0;
  margin: auto 0;
  right: 2px;
  box-shadow: inset 0 0 0 32px #6a6c5f;
  transform-origin: right;
}

.accordion-menu .icon-caretDown:before,
.accordion-menu .icon-caretLeft:before,
.accordion-menu .icon-caretRight:before {
  top: 0;
  transform: rotate(45deg);
}

.accordion-menu .icon-caretDown:after,
.accordion-menu .icon-caretLeft:after,
.accordion-menu .icon-caretRight:after {
  top: 0;
  transform: rotate(-45deg);
}

.accordion-menu .icon-caretRight {
  right: .25rem;
}

.accordion-menu .icon-caretLeft {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.accordion-menu .icon-caretDown {
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
  right: 0;
}

.accordion-menu [class*=icon-]:after,
.accordion-menu [class*=icon-]:before {
  content: '';
  pointer-events: none;
}

.accordion-menu [class*=icon-] {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  font-style: normal;
  text-align: left;
  direction: ltr;
  float: right;
